.slider {
    position: relative;
    height: 100%;

    @media screen and (max-width:767px) {
            padding-bottom: 0;
        }
}

.swiper {
    width: 100%;
    height: 100%;

    @media screen and (min-width:768px) and (max-width:1279px) {
        padding-right: 59px;
        padding-left: 59px;
    }

    @media screen and (min-width:1280px) {
        padding-right: 108px;
        padding-left: 108px;
    }
}
.swiper-wrapper {
    position: relative;
    height: auto;
}
.swiper-slide {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.slider__img {
    width: 85px;
    height: 85px;
    object-fit: cover;
    margin-bottom: 32px;

    @media screen and (min-width:768px) and (max-width:1279px) {
        margin-bottom: 20px;
    }
    @media screen and (min-width:1280px) {
        margin-bottom: 16px;
    }
}

.slider__bg {
    position: relative;
    background: #FAFAFA;
    width: 100%;
    margin-bottom: 16px;
    
    padding: 43px 65px 21px 65px;

    @media screen and (min-width:768px) and (max-width:1279px) {
        margin-bottom: 4px;
        padding: 37px 6px 44px 6px;

        background: rgb(250, 250, 250, 0.4);
    }

    @media screen and (min-width:1280px) {
        margin-bottom: 16px;
        padding: 47px 107px 39px 107px;
        
    }
}

.slider__bg::before {
    content: '';
    position: absolute;
    top: 30px;
    left: 53px;

    display: block;
    width: 54px;
    height: 40px;
    background-image: url("/images/slider/quotes.svg");
    fill: #E1E1E1;
    overflow: hidden;

    @media screen and (min-width:768px) and (max-width:1279px) {
        top: 20px;
        left: 0;

        width: 60px;
        height: 40px;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
    }

    @media screen and (min-width:1280px) {
        top: 32px;
        left: 105px;
    }

}

.slider__txt {
    position: relative;
    font-family: 'DM Sans';
    font-weight: 500;
    font-size: 12px;
    line-height: 2.55;
    text-align: center;
    letter-spacing: 0.04em;
    color: $primary-txt;
    z-index: 2;

    background-image: 
        repeating-linear-gradient(transparent -5px, transparent 25px, #E1E1E1 26px);
    

    @media screen and (min-width:768px) {
        font-size: 14px;
        line-height: 2.16;
        padding-left: 45px;
        padding-right: 49px;
}
    @media screen and (min-width:1280px) {
        font-size: 16px;
        line-height: 1.87;
        padding-left: 37px;
        padding-right: 35px;
    }
}

.slider__dot {
    position: relative;
    display: block;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: $hover-button--red-color;
    margin-bottom: 16px;
    margin-right: auto;
    margin-left: auto;
}

.slider__dot::before {
    content: '';
    position: absolute;
    right: 14px;
    display: block;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: $hover-button--red-color;
    margin-bottom: 16px;
}
.slider__dot::after {
    content: '';
    position: absolute;
    left: 14px;
    display: block;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: $hover-button--red-color;
    margin-bottom: 16px;
}

.slider__txt-contacts {
    font-family: $text-font;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.86;
    text-align: center;
    color: $primary-txt;
        
    @media screen and (min-width:768px) {
    font-size: 16px;
    }
    @media screen and (min-width:1280px) {
    font-size: 20px;
    }
}

.swiper-slide {
    opacity: 0;
}
.swiper-slide-active {
    opacity: 1;

}
.swiper-pagination {
    position: relative;
    display: flex;
    justify-content: center;
    margin-top: 16px;

    @media screen and (min-width:768px) {
        margin-top: 32px;
    }

}

.swiper-pagination-bullet-active {
    background-image: url("/images/slider/home.svg");
    background-size: cover;
    border-radius: unset;
    width: 12px;
    height: 12px;
    background-color: transparent;
}
.swiper-horizontal>.swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal {
    bottom: 0;
}
.swiper-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet{
    margin-left: 0;

    &:nth-child(-n+2) {
        margin-right: 18px;
        
        @media screen and (min-width:1280px) {
        margin-right: 16px;
        }
    }

    &:nth-child(3) {
    margin-right: 0;
    }
}
.swiper-pagination-bullet {
    top: 0;
    display: flex;

    width: 12px;
    height: 12px;
    fill: #D9D9D9;
}


